.ItemImage {
  display: block;
  margin: 15px auto;
  height: 120px;
  max-width: 121%;
  -o-object-fit: contain;
  object-fit: contain;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.ItemName {
  font-size: 22px;
  font-weight: 700;
}

.ItemCost {
  font-size: 20px;
  color: #24c486;
}

.ItemControl {
  font-size: 20px;
  font-weight: 700;
  display: flex;
  justify-content: space-evenly;
  margin-top: 15px;
}

.ItemSell {
  border: none;
  color: #333;
  cursor: auto;
  touch-action: manipulation;
  border-radius: 3px;
  padding: 10px 12px;

  &-Disable {
    background: #f1f2f6 !important;
  }

  &-Enable {
    background: linear-gradient(180deg, #f53b82, #f53b57);
  }
}

.ItemCount {
  font-size: 16px;
  text-align: center;
  padding: 9px 12px;
  border-radius: 3px;
  border: 1px solid #b2bec3;
  color: #333;
  font-family: Roboto, sans-serif;
  max-width: 100%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.ItemBuy {
  cursor: pointer;
  padding: 10px 12px;
  border-radius: 3px;
  color: #fff;
  border: none;
  font-weight: 700;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;

  &-Disable {
    background: #f1f2f6 !important;
  }

  &-Enable {
    background: linear-gradient(180deg, #2ecc71, #1abc9c);

  }
}

.ItemCell {
  max-width: 100%;
  background-color: #fff;
  text-align: center;
  font-size: 18px;
  padding: 20px 15px;
}

@media screen and (max-width: 800px) {
  .ItemControl {
    display: flex;
    flex-direction: column;
  }
  
  .ItemCount {
    margin-top: 5px;
    width: 100%;
  }

  .ItemBuy {
    margin-top: 5px;
  }
}