@import "~antd/dist/antd.css";

* {
  font-size: 16px;
}

*,
:after,
:before {
  box-sizing: border-box;
  margin: 0;
}

a:-webkit-any-link {
  color: -webkit-link;
  cursor: pointer;
  text-decoration: underline;
}

.App {
  background: #f1f2f6;
  color: #333;
  text-align: center;
}

.Content {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  color: #333;
}
