
.Profile {
  background-color: white;
  font-size: 32px;
  background: #fff;
  padding: 30px 20px;
  margin-bottom: 10px;
  font-weight: 700;
  text-align: center;
  line-height: 1.3em;

  &Image {
    display: block;
    user-select: none;
    width: 125px;
    height: 125px;
    margin: 10px auto 25px;
    border-radius: 50%;
  }

  &Selected {
    box-shadow: 0px 10px 25px 15px #E5C7CD;
    border: 3px solid #24c486;
    width: 150px;
    height: 150px;
  }
}

.ProfileList {
  display: flex;
  overflow-x: scroll;

  align-items: center;
  justify-content: space-evenly;
}